@import '../../variables';

.intro {
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto -16rem;
  padding: 1rem 1rem 1rem;

  .intro-picture-wrapper {
    width: 100%;
    overflow: hidden;
    margin: 0 auto 1rem;
    border-radius: 1rem;

    img {
      height: auto;
      width: 100%;
      background-size: contain;
      border-radius: 1rem;
    }
  }

  .description {
    width: 100%;
    margin: 0;
    h1 {
      font-size: 2.5rem;
      text-align: center;
      width: fit-content;
      box-sizing: border-box;
      padding: 1rem 1rem 0rem;
      margin: auto;
      color: $text-dark;
    }
    h2 {
      text-align: center;
      font-size: 1.8rem;
      width: fit-content;
      margin: 1rem auto;
      color: $text-dark;
    }
    p {
      color: $text-dark;
      font-weight: 500;
      font-size: 1.3rem;
    }
    a {
      text-decoration: underline;
      color: $text-dark;
    }
  }
}

// ---------------------------------------------------------------

// when screen is smaller than 768px
@media (max-width: $screen-breakpoint) {
  .intro-desktop {
    display: none;
  }
}

// ---------------------------------------------------------------

// when screen is largen than 768px
@media (min-width: $screen-breakpoint) {
  .intro-mobile {
    display: none;
  }

  .intro {
    //border: 1px solid rgb(0, 0, 255);
    box-sizing: border-box;
    //height: 40rem;
    max-width: 72rem;
    min-height: calc(100vh - 21rem);
    background: transparent;
    margin: -4.5vw auto -16rem;
    padding: 0;
    display: flex;
    align-items: center;

    .intro-picture-wrapper {
      //border: 1px solid red;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      overflow: hidden;
      margin: 0 1rem 1rem;
      position: relative;
      border-radius: 1rem;
      img {
        border-radius: 0;
        margin: 0 0 -0.3rem 0;
        border-radius: 1rem;
      }
    }

    .description {
      width: 90%;
      max-width: 50rem;
      box-sizing: border-box;
      padding: 1rem 0;
      margin: 0 auto;
      border-radius: 1rem;
      position: absolute;
      top: 40%;
      top: calc(25% + 20vh);

      left: 50%;
      transform: translate(-50%, -50%);
      background-color: rgba(255, 255, 255, 0.35);
      // background-color: #31171050;
      backdrop-filter: blur(10px);
      box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.5);
      h1 {
        font-size: 3rem;
        padding: 1rem 1rem 0;
      }
      h2 {
        font-size: 2rem;
      }
      p {
        margin: 1rem auto;
        width: 90%;
        font-size: 1.3rem;
        text-align: center;
      }
    }
  } // intro
}

